import {
  makeApiRequest,
  generateSymbol,
  parseFullSymbol,
} from './helpers.js'
import {
  subscribeOnStream,
  unsubscribeFromStream,
} from './streaming.js'

import { makeReq } from './indicators/binance_open_interest'

const lastBarsCache = new Map()

const configurationData = {
  supported_resolutions: ['1S', '1D', '1W', '1M'],
  exchanges: [{
    value: 'Bitfinex',
    name: 'Bitfinex',
    desc: 'Bitfinex',
  },
  {
    // `exchange` argument for the `searchSymbols` method, if a user selects this exchange
    value: 'Kraken',

    // filter name
    name: 'Kraken',

    // full exchange name displayed in the filter popup
    desc: 'Kraken bitcoin exchange',
  },
  ],
  symbols_types: [{
    name: 'crypto',

    // `symbolType` argument for the `searchSymbols` method, if a user selects this symbol type
    value: 'crypto',
  },
    // ...
  ],
}

async function getAllSymbols () {
  const data = await makeApiRequest('data/v3/all/exchanges')
  let allSymbols = []

  for (const exchange of configurationData.exchanges) {
    const pairs = data.Data[exchange.value].pairs

    for (const leftPairPart of Object.keys(pairs)) {
      const symbols = pairs[leftPairPart].map(rightPairPart => {
        const symbol = generateSymbol(exchange.value, leftPairPart, rightPairPart)
        return {
          symbol: symbol.short,
          full_name: symbol.full,
          description: symbol.short,
          exchange: exchange.value,
          type: 'crypto',
        }
      })
      allSymbols = [...allSymbols, ...symbols]
    }
  }
  return allSymbols
}

export default {
  onReady: (callback) => {
    console.log('[onReady]: Method call')
    setTimeout(() => callback(configurationData))
  },

  searchSymbols: async (
    userInput,
    exchange,
    symbolType,
    onResultReadyCallback,
  ) => {
    console.log('[searchSymbols]: Method call')
    const symbols = await getAllSymbols()
    const newSymbols = symbols.filter(symbol => {
      const isExchangeValid = exchange === '' || symbol.exchange === exchange
      const isFullSymbolContainsInput = symbol.full_name
        .toLowerCase()
        .indexOf(userInput.toLowerCase()) !== -1
      return isExchangeValid && isFullSymbolContainsInput
    })
    onResultReadyCallback(newSymbols)
  },

  resolveSymbol: async (
    symbolName,
    onSymbolResolvedCallback,
    onResolveErrorCallback,
  ) => {
    console.log('[resolveSymbol]: Method call', symbolName)

    // If fragment exists in symbolName, then it's an indicator ticker and shouldn't query the symbol list
    const { exchange, fromSymbol, toSymbol, fragment } = parseFullSymbol(symbolName)
    if (fragment) {
      const name = `${fromSymbol}/${toSymbol}`

      const symbolInfo = {
        name: `${name}#${fragment}`,
        description: `${name}#${fragment}`,
        type: 'crypto',
        session: '24x7',
        timezone: 'Etc/UTC',
        exchange: exchange,
        minmov: 1,
        pricescale: 100,
        has_intraday: false,
        has_no_volume: true,
        has_weekly_and_monthly: false,
        supported_resolutions: configurationData.supported_resolutions,
        volume_precision: 2,
        data_status: 'streaming',
      }

      console.log('[resolveSymbol]: Symbol resolved', symbolName)
      onSymbolResolvedCallback(symbolInfo)
      return
    }

    const symbols = await getAllSymbols()

    const symbolItem = symbols.find(({ full_name }) => full_name === symbolName)
    if (!symbolItem) {
      console.log('[resolveSymbol]: Cannot resolve symbol', symbolName)
      onResolveErrorCallback('cannot resolve symbol')
      return
    }
    const symbolInfo = {
      name: symbolItem.symbol,
      description: symbolItem.description,
      type: symbolItem.type,
      session: '24x7',
      timezone: 'Etc/UTC',
      exchange: symbolItem.exchange,
      minmov: 1,
      pricescale: 100,
      has_intraday: false,
      has_no_volume: true,
      has_weekly_and_monthly: false,
      supported_resolutions: configurationData.supported_resolutions,
      volume_precision: 2,
      data_status: 'streaming',
    }

    console.log('[resolveSymbol]: Symbol resolved', symbolName)
    onSymbolResolvedCallback(symbolInfo)
  },

  getBars: async (symbolInfo, resolution, from, to, onHistoryCallback, onErrorCallback, firstDataRequest) => {
    console.log('[getBars]: Method call', symbolInfo, resolution, from, to)
    const parsedSymbol = parseFullSymbol(symbolInfo.full_name)

    // for debugging ths chart output
    /* function randomBars (from, to) {
      let bars = []
      for (let t = from; t < to; t += 86400) { // from/to are in seconds
        bars = [...bars, {
          time: t * 1000, // milliseconds
          close: Math.random() * 100,
        }]
      }
      return bars
		} */

    // if it has fragment then it's an indicator symbol
    if (parsedSymbol.fragment) {
      const data = await makeReq('/futures/data/openInterestHist', '1d', 'BTCUSDT', 30)

      let bars = []

      data.forEach(b => {
        if (b.timestamp / 1000 >= from && b.timstamp / 1000 < to) {
          bars = [...bars, {
            time: b.timestamp,
            close: b.sumOpenInterest,
          }]
        }
      })

      onHistoryCallback(bars, { noData: false })
      return
    }

    const urlParameters = {
      e: parsedSymbol.exchange,
      fsym: parsedSymbol.fromSymbol,
      tsym: parsedSymbol.toSymbol,
      toTs: to,
      limit: 2000,
    }
    const query = Object.keys(urlParameters)
      .map(name => `${name}=${encodeURIComponent(urlParameters[name])}`)
      .join('&')
    try {
      const data = await makeApiRequest(`data/histoday?${query}`)
      if ((data.Response && data.Response === 'Error') || data.Data.length === 0) {
        // "noData" should be set if there is no data in the requested period.
        onHistoryCallback([], {
          noData: true,
        })
        return
      }
      let bars = []
      data.Data.forEach(bar => {
        if (bar.time >= from && bar.time < to) {
          bars = [...bars, {
            time: bar.time * 1000,
            low: bar.low,
            high: bar.high,
            open: bar.open,
            close: bar.close,
          }]
        }
      })
      if (firstDataRequest) {
        lastBarsCache.set(symbolInfo.full_name, {
          ...bars[bars.length - 1],
        })
      }
      console.log(`[getBars]: returned ${bars.length} bar(s)`)
      onHistoryCallback(bars, {
        noData: false,
      })
    } catch (error) {
      console.log('[getBars]: Get error', error)
      onErrorCallback(error)
    }
  },

  subscribeBars: (
    symbolInfo,
    resolution,
    onRealtimeCallback,
    subscribeUID,
    onResetCacheNeededCallback,
  ) => {
    console.log('[subscribeBars]: Method call with subscribeUID:', subscribeUID)

    const { fragment } = parseFullSymbol(symbolInfo.full_name)

    if (!fragment) {
      subscribeOnStream(
        symbolInfo,
        resolution,
        onRealtimeCallback,
        subscribeUID,
        onResetCacheNeededCallback,
        lastBarsCache.get(symbolInfo.full_name),
      )
    }
  },

  unsubscribeBars: (subscriberUID) => {
    console.log('[unsubscribeBars]: Method call with subscriberUID:', subscriberUID)
    unsubscribeFromStream(subscriberUID)
  },
}
