import fetch from 'node-fetch'

export default (PineJS) => ({
  name: 'BinanceOI',
  metainfo: {
    _metainfoVersion: 42,

    id: 'BinanceOI@tv-basicstudies-1',

    name: 'BinanceOI',
    description: 'Binance Open Interest',
    shortDescription: 'BinanceOI',
    scriptIdPart: '',

    is_price_study: false,
    is_hidden_study: true,
    isCustomIndicator: true,

    isTVScript: false,
    isTVScriptStub: false,
    inputs: [],

    plots: [{
      id: 'plot_0',
      type: 'line',
    }, {
      id: 'plot_hidden',
      type: 'line',
    }],

    defaults: {
      styles: {
        plot_0: {
          linestyle: 0,
          visible: !0,
          linewidth: 2,
          plottype: 2,
          trackPrice: !1,
          transparency: 0,
          color: '#63c5ff',
        },
        plot_hidden: {
          isHidden: !0,
          visible: !1,
          trackPrice: !1,
        },
      },
      precision: 0,
      inputs: {},
    },
    styles: {
      plot_0: {
        title: 'Open interest',
        histogramBase: 0,
      },
    },
  },
  constructor: function () {
    this.init = function (context, input) {
      this._context = context
      this._input = input

      const period = PineJS.Std.period(this._context)

      // https://github.com/tradingview/charting_library/issues/4019
      let t = context.symbol.ticker
      t = t.substring(1, t.length - 2)

      const symbol = t + '#BUYSELL'
      this._context.new_sym(symbol, period, period)
    }

    this.main = function (context, input) {
      this._context = context
      this._input = input

      this._context.select_sym(1)
      return [PineJS.Std.close(this._context)]
    }
  },
})

export async function makeReq (endpoint, period, symbol, limit) {
  const proxy = 'https://blep-cors.herokuapp.com'
  const bnbAPI = 'https://fapi.binance.com'

  try {
    const res = await fetch(`${proxy}/${bnbAPI}${endpoint}/?symbol=${symbol}&period=${period}&limit=${limit}`)
    if (!res.ok) throw Error(res.statusText)
    return res.json()
  } catch (error) {
    console.error(error)
  }
}

